<template>
	<form @submit.prevent="submitForm">
		<div>
			<label for="username">username: </label>
			<input type="text" id="username" v-model="user.username" />
		</div>
		<div>
			<label for="password">password: </label>
			<input type="text" id="password" v-model="user.password" />
		</div>
		<button :disabled="!isUserNameValid || !user.password" type="submit">
			Loginüëç
		</button>
		<p>{{ logMessage }}</p>
	</form>
</template>

<script>
import { loginUser } from '@/api/index';
import { validateEmail } from '@/utils/validation';

export default {
	data() {
		return {
			user: {
				username: '',
				password: '',
			},
			logMessage: '',
		};
	},
	// Ïú†Ìö®ÏÑ± Í≤ÄÏÇ¨, store Îì±Îì±Îäî Î≥¥ÌÜµ computed
	computed: {
		// ÏßÄÏÜçÏ†ÅÏúºÎ°ú v-modelÏ≤òÎüº Í≤ÄÏÇ¨Ìï®
		isUserNameValid() {
			console.log(validateEmail(this.user.name));
			return validateEmail(this.user.username);
		},
	},
	methods: {
		async submitForm() {
			try {
				const { data } = await loginUser(this.user);
				console.log(data.usrename);
				//router ÎßÅÌÅ¨ to Î°ú Ïù¥Îèô //paramÏù¥ÎÇò queryÎèÑ ÎÑòÍ∏∏ÏàòÏûàÏùå
				this.$router.push('/main');
				// this.logMessage = `${data.user.nickname}Îãò ÌôòÏòÅÌï©ÎãàÎã§!`;
			} catch ({ response }) {
				this.logMessage = `${response.data}`;
			} finally {
				this.initForm();
			}
		},
		initForm() {
			this.user = {
				username: '',
				password: '',
			};
		},
	},
};
</script>

<style scoped>
.btn {
	color: white;
}
</style>
